import "./styles/styles.css";
import "./components/note-header";
import "./components/note-input";
import "./components/note-item";

// Fetch and display notes
async function fetchNotes() {
  const response = await fetch("https://notes-api.dicoding.dev/v2/notes");
  const result = await response.json();
  if (result.status === "success") {
    const notes = result.data;
    displayNotes(notes);
  } else {
    console.error("Failed to fetch notes");
  }
}

// Display notes in the container
function displayNotes(notes) {
  const notesContainer = document.getElementById("notesContainer");
  notesContainer.innerHTML = "";
  notes.forEach((note) => {
    const noteElement = document.createElement("note-item");
    noteElement.note = {
      ...note,
      date: new Date(note.createdAt).toLocaleString(),
    };
    notesContainer.appendChild(noteElement);
  });
}

// Handle note addition
document.addEventListener("note-added", async (event) => {
  const note = event.detail;
  const response = await fetch("https://notes-api.dicoding.dev/v2/notes", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(note),
  });
  const result = await response.json();
  if (result.status === "success") {
    displayNotes([result.data, ...(await fetchNotes())]);
  } else {
    console.error("Failed to add note");
  }
});

// Handle note editing
document.addEventListener("edit-note", async (event) => {
  const noteToEdit = event.detail;
  const response = await fetch(
    `https://notes-api.dicoding.dev/v2/notes/${noteToEdit.id}`,
    {
      method: "GET",
    }
  );
  const result = await response.json();
  if (result.status === "success") {
    const { title, body, createdAt } = result.data;
    const editPopup = document.getElementById("edit-popup");
    editPopup.querySelector(".popup-content").innerHTML = `
      <h2>Edit Note</h2>
      <form id="edit-form">
        <label for="edit-title">Note Title</label>
        <input type="text" id="edit-title" name="title" value="${title}" required />
        <span id="title-error" class="error-message"></span>

        <label for="edit-body">Note Description</label>
        <textarea id="edit-body" name="body" rows="4" required>${body}</textarea>
        <span id="body-error" class="error-message"></span>

        <label for="edit-created-at">Date and Time</label>
        <input type="datetime-local" id="edit-created-at" name="created-at" value="${new Date(
          createdAt
        )
          .toISOString()
          .slice(0, 16)}" />

        <button type="submit">Save Changes</button>
        <button type="button" id="cancel-edit">Cancel</button>
      </form>
    `;

    document.getElementById("cancel-edit").onclick = () => {
      editPopup.style.display = "none";
    };

    document.getElementById("edit-form").onsubmit = async (e) => {
      e.preventDefault();
      const updatedTitle = e.target.querySelector("#edit-title").value;
      const updatedBody = e.target.querySelector("#edit-body").value;
      const updatedCreatedAt = e.target.querySelector("#edit-created-at").value;

      const response = await fetch(
        `https://notes-api.dicoding.dev/v2/notes/${noteToEdit.id}`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            title: updatedTitle,
            body: updatedBody,
            createdAt: updatedCreatedAt,
          }),
        }
      );

      const result = await response.json();
      if (result.status === "success") {
        displayNotes(await fetchNotes());
        editPopup.style.display = "none";
      } else {
        console.error("Failed to update note");
      }
    };

    editPopup.style.display = "flex";
  } else {
    console.error("Failed to fetch note");
  }
});

// Handle note deletion
document.addEventListener("delete-note", async (event) => {
  const noteToDelete = event.detail;
  const confirmDeletePopup = document.getElementById("confirm-delete-popup");
  confirmDeletePopup.querySelector(
    "#delete-confirm-message"
  ).innerText = `Are you sure you want to delete the note titled "${noteToDelete.title}"?`;

  document.getElementById("confirm-delete").onclick = async () => {
    const response = await fetch(
      `https://notes-api.dicoding.dev/v2/notes/${noteToDelete.id}`,
      {
        method: "DELETE",
      }
    );
    const result = await response.json();
    if (result.status === "success") {
      displayNotes(await fetchNotes());
      confirmDeletePopup.style.display = "none";
    } else {
      console.error("Failed to delete note");
    }
  };

  document.getElementById("cancel-delete").onclick = () => {
    confirmDeletePopup.style.display = "none";
  };

  confirmDeletePopup.style.display = "flex";
});

// Initial fetch
fetchNotes();
